import {
  ConnectedPosition,
  FlexibleConnectedPositionStrategyOrigin,
  RepositionScrollStrategyConfig
} from '@angular/cdk/overlay';

export type AdaptOverlayConnectedPositionName = 'top' | 'top-left' | 'top-right' | 'left' | 'left-top' | 'left-bottom' |
  'right' | 'right-top' | 'right-bottom' | 'bottom' | 'bottom-left' | 'bottom-right' | 'start' | 'start-top' |
  'start-bottom' | 'top-start' | 'top-end' | 'end' | 'end-top' | 'end-bottom' | 'bottom-start' | 'bottom-end';

export type AdaptOverlayConnectedPositions = { [key in AdaptOverlayConnectedPositionName]: ConnectedPosition };

export type AdaptOverlayPositionStrategy = 'float' | 'center' | 'connected';

export type AdaptOverlayScrollStrategy = 'reposition' | 'block' | 'close';

export interface AdaptOverlayConfig {
  origin?: FlexibleConnectedPositionStrategyOrigin;
  transformOriginSelector?: string;
  positionStrategy?: AdaptOverlayPositionStrategy;
  connectedPositions?: ConnectedPosition[];
  scrollStrategy?: AdaptOverlayScrollStrategy;
  repositionScrollStrategyConfig?: RepositionScrollStrategyConfig;
}
